import React from "react";
import classnames from "classnames";
import { Consumer } from '../../Config/Context';


import {
  Button,
  Collapse,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container
} from "reactstrap";

function IndexNavbar() {
  const [navbarColor, setNavbarColor] = React.useState("navbar-transparent");
  const [navbarCollapse, setNavbarCollapse] = React.useState(false);

  const toggleNavbarCollapse = () => {
    setNavbarCollapse(!navbarCollapse);
    document.documentElement.classList.toggle("nav-open");
  };

  React.useEffect(() => {
    const updateNavbarColor = () => {
      if (
        document.documentElement.scrollTop > 299 ||
        document.body.scrollTop > 299
      ) {
        setNavbarColor("");
      } else if (
        document.documentElement.scrollTop < 300 ||
        document.body.scrollTop < 300
      ) {
        setNavbarColor("navbar-transparent");
      }
    };

    window.addEventListener("scroll", updateNavbarColor);
    return function cleanup() {
      window.removeEventListener("scroll", updateNavbarColor);
    };
  });

  return (
  <Consumer>
    { ({idioma,Config,handleClick}) => {
      let menu = Config.header[idioma].menu
      // console.log(menu);
      return(
        <Navbar className={classnames("fixed-top", navbarColor)} expand="lg">
            <div className={navbarColor?"":"iconBrandCenter gris"}></div>
            <Container>
              <div className="navbar-translate">
                <NavbarBrand
                  data-placement="bottom"
                  href="/index"
                  title="Coded by ..."
                >
                  <div className={navbarColor?"gova blanco gova-navbar grande":"gova gris gova-navbar grande"}></div>
                </NavbarBrand>
                <button
                  aria-expanded={navbarCollapse}
                  className={classnames("navbar-toggler navbar-toggler", {
                    toggled: navbarCollapse
                  })}
                  onClick={toggleNavbarCollapse}
                >
                  <span className="navbar-toggler-bar bar1" />
                  <span className="navbar-toggler-bar bar2" />
                  <span className="navbar-toggler-bar bar3" />
                </button>
              </div>
              <Collapse
                className="justify-content-end"
                navbar
                isOpen={navbarCollapse}
              >
                <Nav navbar>
                  {menu.map( (current,index,array) => {
                    let retorno=""
                    switch(current.tipo){
                      case "icon":
                        retorno=<NavitemIcon {...current} key={index+"_tipo_"+current.tipo} />
                      break;
                      case "menu":
                        retorno=<NavitemMenu {...current} key={index+"_tipo_"+current.tipo}/>
                      break;
                      case "idioma":
                        retorno=<NavitemIdioma {...current} key={index+"_tipo_"+current.tipo}/>
                      break;
                      default:
                      break
                    }
                    return retorno
                  })}            
                </Nav>
              </Collapse>
            </Container>
          </Navbar>
      )
    }}
  </Consumer>
  );
}

function NavitemMenu(props){
  let {href,descripcion,iconClass} = props;
  return(
              <NavItem>
                  <NavLink
                    href={href}
                  >
                    <i className={iconClass} /> {descripcion}
                  </NavLink>
                </NavItem>
  )
}

function NavitemIcon(props){
  let {href,title,descripcion,tipo,iconClass} =  props
  return(
              <NavItem>
                <NavLink
                  key={descripcion+"_key_type_"+tipo}
                  data-placement="bottom"
                  href={href}
                  target="_blank"
                  title={title}
                >
                  <i className={iconClass} />
                  <p className="d-lg-none">{descripcion}</p>
                </NavLink>
              </NavItem>
  )
}
function NavitemIdioma(props){
  let {id,descripcion} = props

  return(
                <Consumer>
                { ({idioma,handleClick}) => {
                      let outline=true
                      if(idioma===id)
                        outline=false
                      let propsDefault = { 
                            id:id,
                            className:"btn-round",
                            color:"danger",
                            outline:outline,
                      } 
                  return(
                    <NavItem>
                      <Button
                        { ...propsDefault}
                        onClick={handleClick}                   
                      >
                        {descripcion}
                      </Button>
                    </NavItem>
                  )
                }}
                </Consumer>
  )
}

export default IndexNavbar;
